import React from "react"
import GatsbyLink from "gatsby-link"
import Icon from "../components/Icons"
import styled from "styled-components"

import LinkBlog from "../components/LinkBlog"

export default function Tags({ pathContext }) {
  const { posts, post, tag } = pathContext
  if (tag) {
    return (
      <div>
        <h1>
          {post.length} post{post.length === 1 ? "" : "s"} tagged with {tag}
        </h1>
        <ul>
          {post.map(({ id, frontmatter, excerpt }) => {
            return (
              <li key={id}>
                <h1>
                  <GatsbyLink to={frontmatter.path}>
                    {frontmatter.title}
                  </GatsbyLink>
                </h1>
                <p>{excerpt}</p>
              </li>
            )
          })}
        </ul>
        <LinkBlog to="/tags">All tags</LinkBlog>
      </div>
    )
  }
  return (
    <div>
      <h1>Tags</h1>
      <ul className="tags">
        {Object.keys(posts).map(tagName => {
          const tags = posts[tagName]
          return (
            <li key={tagName}>
              <GatsbyLink to={`/tags/${tagName}`}>{tagName}</GatsbyLink>
            </li>
          )
        })}
      </ul>
      <LinkBlog to="/">
        <Icon name="home" color="#6079F2" /> All posts
      </LinkBlog>
    </div>
  )
}



